.section-precios {
  background-color: $white;
  padding-top: 3rem;
  padding-bottom: 3rem;
  .items-price {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: auto;
    padding-top: 3rem;
    .dog-size {
      .talla {
        cursor: pointer;
        font-size: 2rem;
        font-weight: 200;
        color: white;
        background-color: $blue;
        border-radius: 8px;
        margin-bottom: 0;
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 1rem auto;
        &:hover {
          background-color: $yellow;
        }
        &.active {
          background-color: $yellow;
        }
        @media (min-width: 768px) {
          font-size: 3rem;
          border-radius: 15px;
        }
      }
    }
    .text-talla {
      font-size: .8rem;
      color: $blue;
      font-weight: 200;
      @media (min-width: 768px) {
        font-size: 1.2rem;
      }
    }
    .peso-talla {
      font-size: .8rem;
      color: $blue;
      font-weight: bold;
      @media (min-width: 768px) {
        font-size: 1.5rem;
      }
    }
    .triangulo {
      display: none;
      width: 0;
      height: 0;
      border-right: 10px solid transparent;
      border-top: 10px solid transparent;
      border-left: 10px solid transparent;
      border-bottom: 10px solid #f7d054;
      margin: 0 auto;
      @media (min-width: 768px) {
        border-right: 20px solid transparent;
        border-top: 20px solid transparent;
        border-left: 20px solid transparent;
        border-bottom: 20px solid #f7d054;
      }
      @media (min-width: 992px) {
        border-right: 30px solid transparent;
        border-top: 30px solid transparent;
        border-left: 30px solid transparent;
        border-bottom: 30px solid #f7d054;
      }
    }
  }
  .content-price {
    padding: 1.5rem 0;
    background-color: $yellow;
    border-radius: 15px;
    width: 95%;
    margin: 0 auto;
    @media (min-width: 525px) {
      width: 90%;
    }
    .title-datos {
      color: $blue;
      font-weight: bold;
      margin-bottom: 1rem;
      font-size: 2rem;
    }
    .text-datos {
      font-size: 1rem;
      color: $blue;
      @media (min-width: 768px) {
        font-size: 1.2rem;
      }
    }
  }
}