.seccion-slider-menu {
  background-color: $white;
  padding-top: 3rem;
  padding-bottom: 3rem;
  p.title-blue {
    text-align: center;
    color: $blue;
    margin: 0;
    font-size: 2rem;
    line-height: 1;
  }
  .content-slider-menu-img {
    width: 80%;
    margin: 0 auto;
    text-align: center;
    .menu-img {
      margin-left: -40px;
      margin-right: -40px;
      .slick-list {
        .slick-track {
          padding-top: 30px;
          padding-bottom: 30px;
          .slick-slide {
            position: relative;
            margin: 0 -20px;
            opacity: 0;
            transition: all 0.3s;
            transition-delay: 0.3s;
            z-index: -1;
            background-color: $yellow;
            border-radius: 50%;
            display: flex;
            align-items: center;
            div {
              height: 100%;
              display: flex;
              align-items: center;
              .slide {
                height: 100%;
              }
            }
            &.slick-active {
              // z-ind<ex: 1;
              opacity: 1;
              cursor: pointer;
            }
            &.slick-center {
              z-index: 2;
              transform: scale(1.2);
              background-color: $blue;
              .content-img {
                border: 4px solid $white;
                border-radius: 50%;
                width: 100%;
                height: 100%;
              }
            }
            img {
              width: 100%;
            }
            &.zIndex1 {
              z-index: 1;
            }
            &.opacity05 {
              opacity: 0.5;
            }
          }
        }
      }
      .slick-prev{
        left: 0;
        @media (min-width: 768px) {
          left: -5%;
        }
      }
      .slick-next {
        right: 0;
        @media (min-width: 768px) {
          right: -5%;
        }
      }
      .slick-prev img,
      .slick-next img {
        width: 20px;
        height: 40px;
      }
      .slick-prev:before,
      .slick-next:before {
        display: none;
      }
    }
  }
  .content-slider-menu-text {
    width: 80%;
    margin: 0 auto;
    text-align: center;
    @media (min-width: 768px) {
      width: 65%;
    }
    @media (min-width: 992px) {
      width: 50%;
    }
    .menu-text {
      .slick-slide {
        padding: 0 2rem;
        p {
          color: $blue;
        }
        h3 {
          color: $blue;
          margin: 0;
          font-weight: bold;
        }
        .text {
          color: $gray;
        }
      }
      
    }
  }
}