.btn-rounded {
  border-radius: 50px;
  line-height: 1.75;
}
.btn-custom {
  font-size: .8rem;
}
.btn-yellow-white {
  padding-left: 1rem;
  padding-right: 1rem;
  color: $white;
  background: $yellow;
  transition: all 0.3s;
  border: none;
  &:hover {
    color: $blue;
  }
  &:before {
    background: $white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: $blue;
  }
}
.btn-yellow-blue {
  padding-left: 4rem;
  padding-right: 4rem;
  color: $blue;
  font-weight: bold;
  background: $yellow;
  border: none;
  transition: all 0.3s;
  &:hover {
    color: $blue;
  }
  &:before {
    background: $white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: $blue;
  }
}
.btn-yellowgr-white {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $blue;
  font-weight: bold;
  background: linear-gradient(180deg, rgba(247,208,84,1) 0%, rgba(251,131,27,1) 100%);
  border: none;
  transition: all 0.3s;
  &:hover {
    color: $blue;
    box-shadow: 0 0 1px 1px $yellow;
  }
  &:before {
    background: $white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: $blue;
  }
}
.btn-blue-white {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $white;
  font-weight: bold;
  background: radial-gradient(circle, rgba(56,103,193,1) 29%, rgba(44,70,154,1) 100%);
  border: none;
  transition: all 0.3s;
  &:hover {
    color: $blue;
    box-shadow: 0 0 1px 1px $blue;
  }
  &:before {
    background: $white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: $blue;
  }
}

.btn-center-out {
  transform: perspective(1px) translateZ(0);
  overflow: hidden;
}
.btn-center-out:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: scale(0);
  transition-property: transform;
  transition-duration: 0.3s;
}
.btn-center-out:hover:before,
.btn-center-out:focus:before,
.btn-center-out:active:before {
  transform: scale(1.15);
}
