.section-footer1 {
  background-color: #223171;
  padding-top: 3rem;
  padding-bottom: 3rem;
  h5 {
    color: $white;
    font-weight: bold;
  }
  p {
    color: $white;
    font-weight: 200;
  }
  ul {
    padding-left: 0;
  }
  li {
    list-style: none;
    margin-bottom: .3rem;
    .items-menu {
      color: $white;
      font-weight: 200;
      text-decoration: none;
      &:hover {
        color: $yellow;
      }
    }
  }
  small {
    color: $white;
  }
  .logo-footer {
    width: 80%;
    @media (max-width: 767.98px) {
      width: 50%;
      max-width: 400px;
    }
  }
}
.section-footer2 {
  background-color: #0f1e4c;
  padding: .5rem;
  p {
    color: $white;
    font-weight: 200;
  }
}