.section-caracteristicas {
  background-image: url('../images/bg-caracteristicas.png');
  background-size: cover;
  padding-top: 11rem;
  padding-bottom: 5rem;
  @media (min-width: 992px) {
    background-size: 100% 100%;
  }
  .items-caract {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    @media (min-width: 768px) {
      flex-wrap: nowrap;
    }
    .circle {
      flex: 30%;
      max-width: 30%;
      @media (min-width: 768px) {
        flex: 20%;
        max-width: 20%;
      }
      .circle-caract {
        padding: 2rem;
        background-color: $white;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .3s;
        &:hover {
          transform: scale(1.05);
        }
      }
      .text-caract {
        color: $white;
        font-weight: 200;
        font-size: .8rem;
        height: 40px;
        @media (min-width: 1200px) {
          font-size: 1rem;
        }
      }
    }
    .circle.circle1 {
      margin-right: 1rem;
      @media (min-width: 768px) {
        margin-right: 1rem;
      }
      @media (min-width: 992px) {
        margin-right: 5rem;
      }
    }
  }
}