.section-veterinaria {
  background-color: $gray-light;
  padding-top: 3rem;
  padding-bottom: 3rem;
  .fondo-circle {
    margin-top: 3rem;
    position: relative;
    .circle-yellow {
      position: absolute;
      background-color: $yellow;
      width: 8vw;
      height: 8vw;
      border-radius: 50%;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      display: none;
      @media (min-width: 992px) {
        display: block;
      }
    }
    .col-text-club {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      .content-text-club {
        padding: 2rem;
        .text-parrafos-club {
          color: $gray;
          margin-bottom: 0;
          @media (min-width: 992px) and (max-width: 1199.98px) {
            font-size: .8rem;
          }
        }
        .text-club {
          color: $blue;
          margin-top: 2rem;
          font-weight: bold;
        }
      }
    }
    .img1 {
      border-top-left-radius: 50px;
      border-top-right-radius: 50px;
      border-bottom-right-radius: 50px;
    }
    .img2 {
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
      border-bottom-right-radius: 50px;
    }
    .col-img-club {
      z-index: 2;
    }
  }
  .content-img {
    img {
      width: 30%;
    }
  }
}