.section-prueba {
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: $white;
  position: relative;
  .circle-prueba {
    border-radius: 50%;
    background: linear-gradient(180deg, rgba(247,208,84,1) 0%, rgba(251,131,27,1) 100%);
    width: 40vw;
    height: 40vw;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    @media (min-width: 576px){
      height: 200px;
      width: 200px;
    }
    @media (min-width: 768px){
      position: absolute;
      height: 200px;
      width: 200px;
      top: 0;
      transform: translateX(25%) translateY(-33%);
      left: 0;
    }
    .title-price {
      color: $blue;
      font-weight: bold;
      font-size: 1.5rem;
      margin-bottom: 0;
      text-align: center;
    }
    .price {
      color: $white;
      margin-bottom: 0;
      font-weight: bold;
      font-size: 2rem;
    }
  }
  .content-prueba {
    padding-top: 3rem;
    .items-prueba {
      display: grid;
      grid-template-columns: 1fr 1fr 2fr 1fr 1fr;
      grid-template-rows: auto;
      grid-gap: 10px;
      grid-template-areas:
      ". . text1 . ."
      ". . fu . ."
      "text2 fl imagen fr text4"
      ". . fd . ."
      ". . text3 . .";
      margin-top: 3rem;
      justify-content: center;
      align-items: center;
      .text-prueba {
        color: $gray;
        margin-bottom: 0;
        font-size: .8rem;
        @media (min-width: 576px) {
          font-size: 1rem;
        }
      }
      .text1 {
        grid-area: text1;
      }
      .text2 {
        grid-area: text2;
      }
      .text3 {
        grid-area: text3;
      }
      .text4 {
        grid-area: text4;
      }
      .fu {
        grid-area: fu;
        margin-bottom: 3rem;
        img {
          width: 7%;
        }
      }
      .fl {
        grid-area: fl;
        img {
          width: 80%;
        }
      }
      .fd {
        grid-area: fd;
        margin-top: 3rem;
        img {
          width: 7%;
        }
      }
      .fr {
        grid-area: fr;
        img {
          width: 80%;
        }
      }
      .imagen {
        background-color: $gray-light;
        grid-area: imagen;
        border-radius: 50%;
        padding: 3rem 0;
        margin: 0 auto;
      }
    }
  }
}