.section-banner {
  background-image: url(../images/bg-blue.png);
  background-size: cover;
  padding-top: 2rem;
  padding-bottom: 2rem;
  .title-blanco-com {
    color: $white;
    font-size: 2rem;
    font-weight: 200;
    margin-bottom: 0;
  }
  .subtitle-com {
    color: $white;
    font-weight: 200;
    margin-bottom: 0;
  }
  .estrellas {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    img {
      width: 7vw;
      margin-right: .5rem;
      margin-left: .5rem;
      @media (min-width: 992px) {
        width: 3vw;
      }
    }
  }
}
