.section-welcome {
  background-image: url('../images/welcome/bg-welcome.png');
  background-size: cover;
  background-position: bottom center;
  padding-top: calc(135px + 1rem);
  padding-bottom: 5rem;
  @media (min-width: 1200px) {
    padding-top: calc(135px + 4rem);
    padding-bottom: 0;
  }
  .title-welcome {
    color: $white;
    font-weight: bold;
    font-size: 3rem;
    line-height: 1;
  }
  .subtitle-welcome {
    color: $white;
    font-weight: 200;
  }
  .text-dog {
    color: $white;
    font-weight: 200;
    font-size: .8rem;
    @media (min-width: 1200px) {
      font-size: 1rem;
    }
  }
  .items-dogs {
    display: flex;
    .circle {
      margin-right: 1rem;
      flex: 15%;
      max-width: 15%;
      @media (min-width: 768px) {
        flex: 20%;
        max-width: 20%;
      }
    }
    .circle-dogs {
      padding: 1rem;
      background-color: $white;
      border-radius: 50%;
    }
  }
  .big-dog {
    text-align: center;
    img {
      width: 55%;
    }
  }
}
