.section-galeria {
  padding-top: 3rem;
  padding-bottom: 3rem;
  @media (max-width: 424.98px) {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  .galeria-ppal {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(6, 1fr);
    grid-template-areas:
      "item1 item1 item2"
      "item1 item1 item2"
      "item1 item1 item2"
      "item3 item4 item2"
      "item3 item4 item5"
      "item3 item4 item5";
    gap: 20px;
    @media (max-width: 991.98px) {
      gap: 15px;
    }
    @media (max-width: 767.98px) {
      gap: 10px;
    }
    @media (max-width: 424.98px) {
      gap: 5px;
    }
    .item {
      position: relative;
      transition: all .5s;
      .placa-gray {
        width: 100%;
        height: 100%;
        position: absolute;
        display: none;
        background-color: rgba($color: #000, $alpha: 0.6);
        transition: all .5s;
      }
      .agrandar-img {
        position: absolute;
        width: 5vw;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        opacity: 0;
      }
      &:hover {
        .placa-gray {
          display: inline-block;
        }
        .agrandar-img {
          opacity: 1;
        }
      }
    }
    .item1 {
      grid-area: item1;
    }
    .item2 {
      grid-area: item2;
    }
    .item3 {
      grid-area: item3;
    }
    .item4 {
      grid-area: item4;
    }
    .item5 {
      grid-area: item5;
    }
  }
  .slick-dots {
    li {
      button {
        &:before {
          font-size: 20px;
          color: $blue;
          opacity: 1;
        }
      }
    }
  }
  .slick-dots {
    li.slick-active {
      button {
        &:before {
          opacity: .75;
          color: $gray;
        } 
      }
    }
  }
}
