.section-proceso {
  padding-top: 3rem;
  padding-bottom: 3rem;
  background: radial-gradient(circle, rgba(56,103,193,1) 29%, rgba(44,70,154,1) 100%);
  .subtitle {
    color: $white;
    margin-bottom: 1rem;
    font-size: 2rem;
  }
  .item-card {
    padding: 2rem;
    .content-title {
      height: 3rem;
    }
    .content-card {
      position: relative;
      background-color:$white;
      padding: 3rem;
      border-radius: 20px;
      img {
        width: 100%;
      }
    }
    .number-card {
      background-color: $yellow;
      border-radius: 50%;
      position: absolute;
      width: 20%;
      height: 20%;
      bottom: 0;
      left: 0;
      transform: translateX(-40%) translateY(40%);
      display: flex;
      align-items: center;
      justify-content: center;
      @media (min-width: 768px) {
        width: 30%;
        height: 30%;
      }
    }
  }
  .title-card {
    color: $yellow;
    font-weight: bold;
  }
  .text-card {
    color: $white;
    font-weight: 200;
    font-size: .8rem;
    margin-top: 2rem;
  }
}