.section-comentarios {
  background-color: $gray-light;
  padding-top: 3rem;
  padding-bottom: 3rem;
  .fondo-circle {
    margin-top: 3rem;
    .col-text-comentarios {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      .content-text-comentarios {
        padding: 2rem;
        .text-parrafos-comentarios {
          color: $gray;
          margin-bottom: 0;
          @media (min-width: 992px) and (max-width: 1199.98px) {
            font-size: .8rem;
          }
        }
      }
    }
    .img1 {
      border-top-left-radius: 50px;
      border-top-right-radius: 50px;
      border-bottom-right-radius: 50px;
    }
    .img2 {
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
      border-bottom-right-radius: 50px;
    }
    .col-img-comentarios {
      position: relative;
      padding: 1rem 0;
      .dog-name {
        background: radial-gradient(circle, rgba(56,103,193,1) 29%, rgba(44,70,154,1) 100%);
        width: 40%;
        text-align: center;
        padding: .8rem 0;
        border-radius: 50px;
        color: $white;
        font-weight: bold;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%) translateY(15%);
        position: absolute;
      }
    }
  }
}